"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import InputGroup from '@/components/ui/InputGroup';
import SaveReportButton from '@/components/SaveReportButton';
import { calculateSalaryBreakup, formatCurrency } from '@/utils/formulas';
import { ArrowLeft, Calculator, PieChart as PieIcon, Wallet } from 'lucide-react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);

export default function SalaryCalculator() {
    const [ctc, setCtc] = useState(1200000);
    const [result, setResult] = useState<any>(null);

    useEffect(() => {
        const data = calculateSalaryBreakup(ctc);
        setResult(data);
    }, [ctc]);

    const chartData = {
        labels: ['Basic Salary', 'HRA', 'Special Allowance', 'Employer PF'],
        datasets: [
            {
                data: [
                    result?.yearly.basic || 0,
                    result?.yearly.hra || 0,
                    result?.yearly.specialAllowance || 0,
                    result?.yearly.employerPf || 0,
                ],
                backgroundColor: [
                    '#8b5cf6', // Violet
                    '#ec4899', // Pink
                    '#f59e0b', // Amber
                    '#64748b', // Slate (PF)
                ],
                borderWidth: 0,
            },
        ],
    };

    const chartOptions = {
        cutout: '75%',
        maintainAspectRatio: false,
        plugins: {
            legend: {
                position: 'bottom' as const,
                labels: {
                    usePointStyle: true,
                    padding: 15,
                    boxWidth: 8,
                    font: {
                        family: "'Inter', sans-serif",
                        size: 11,
                        weight: 600
                    },
                    color: '#64748b'
                }
            },
            tooltip: {
                backgroundColor: '#1e293b',
                padding: 12,
                cornerRadius: 8,
                callbacks: {
                    label: function (context: any) {
                        return ' ' + context.label + ': ' + formatCurrency(context.raw);
                    }
                }
            }
        },
        layout: {
            padding: {
                top: 10,
                bottom: 10
            }
        }
    };

    return (
        <div className="min-h-screen bg-[#F8FAFC] py-12 font-sans">
            <div className="max-w-6xl mx-auto px-4">
                <Link href="/" className="inline-flex items-center text-sm font-bold text-slate-400 hover:text-purple-600 mb-8 transition-colors group">
                    <ArrowLeft className="w-4 h-4 mr-1 group-hover:-translate-x-1 transition-transform" /> Back to Home
                </Link>

                <div className="grid grid-cols-1 xl:grid-cols-12 gap-8">

                    <div className="xl:col-span-4 space-y-6">
                        <div>
                            <div className="inline-flex items-center gap-2 px-3 py-1.5 rounded-full bg-purple-100 text-purple-700 text-xs font-extrabold mb-4 uppercase tracking-wide">
                                <Wallet className="w-3 h-3" /> Income Tools
                            </div>
                            <h1 className="text-4xl font-black text-slate-900 mb-3 tracking-tight">Salary Calculator</h1>
                            <p className="text-slate-500 font-medium leading-relaxed">
                                Understand your in-hand salary from your annual CTC.
                            </p>
                        </div>

                        <div className="bg-white p-8 rounded-3xl shadow-xl shadow-slate-200/60 border border-slate-100">
                            <InputGroup
                                label="Annual CTC"
                                value={ctc}
                                onChange={setCtc}
                                type="currency"
                                min={100000} max={10000000} step={10000}
                                variant="purple"
                            />

                            <div className="bg-purple-50 p-6 rounded-2xl border border-purple-100 mt-6">
                                <h4 className="text-purple-800 font-bold text-sm mb-4 flex items-center gap-2">
                                    <PieIcon className="w-4 h-4" /> CTC Distribution
                                </h4>
                                <div className="h-80 relative">
                                    <Doughnut data={chartData} options={chartOptions} />
                                    <div className="absolute inset-0 flex items-center justify-center pointer-events-none pb-12">
                                        <div className="text-center">
                                            <p className="text-[10px] text-slate-400 font-bold uppercase tracking-wider mb-1">Gross Monthly</p>
                                            <p className="text-lg font-black text-slate-700">{formatCurrency(result?.monthly.grossSalary || 0)}</p>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>

                        <div className="mt-6">
                            <SaveReportButton
                                reportData={result}
                                calculatorType="Salary Calculator"
                                disabled={!result}
                            />
                        </div>

                    </div>
                </div>

                <div className="xl:col-span-8">
                    {/* Summary Card */}
                    <div className="bg-gradient-to-br from-purple-600 to-indigo-700 rounded-[2.5rem] p-8 sm:p-10 text-white shadow-2xl shadow-purple-500/30 relative overflow-hidden mb-8">
                        <div className="absolute top-0 right-0 -mt-20 -mr-20 w-80 h-80 bg-white opacity-10 rounded-full blur-3xl animate-pulse"></div>

                        <div className="relative z-10 grid sm:grid-cols-2 gap-8 items-center">
                            <div>
                                <p className="text-purple-100 font-semibold mb-2 tracking-wide text-xs sm:text-sm uppercase">
                                    Monthly In-Hand Salary
                                </p>
                                <p className="text-5xl sm:text-6xl font-black tracking-tighter mb-4 drop-shadow-sm leading-tight">
                                    {formatCurrency(result?.monthly.netSalary || 0)}
                                </p>
                                <p className="text-purple-200 text-sm font-medium">
                                    * Before Income Tax deductions
                                </p>
                            </div>
                            <div className="space-y-3">
                                <div className="flex justify-between items-center bg-white/10 backdrop-blur-md p-4 rounded-2xl border border-white/10">
                                    <span className="text-purple-50 font-medium">Monthly Gross</span>
                                    <span className="font-bold text-xl">{formatCurrency(result?.monthly.grossSalary || 0)}</span>
                                </div>
                                <div className="flex justify-between items-center bg-white/10 backdrop-blur-md p-4 rounded-2xl border border-white/10">
                                    <span className="text-purple-50 font-medium">Total Deductions</span>
                                    <span className="font-bold text-xl text-red-200">-{formatCurrency(result?.monthly.totalDeductions || 0)}</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Detailed Breakdown Table */}
                    <div className="bg-white rounded-3xl shadow-sm border border-slate-100 overflow-hidden">
                        <div className="p-6 border-b border-slate-100 bg-slate-50/50">
                            <h3 className="font-bold text-slate-800 text-lg">Detailed Salary Breakup</h3>
                        </div>
                        <div className="overflow-x-auto">
                            <table className="w-full text-left border-collapse">
                                <thead>
                                    <tr className="text-xs font-bold text-slate-400 uppercase tracking-wider border-b border-slate-100">
                                        <th className="p-4 pl-8">Component</th>
                                        <th className="p-4 text-right">Monthly</th>
                                        <th className="p-4 pr-8 text-right">Yearly</th>
                                    </tr>
                                </thead>
                                <tbody className="text-sm font-medium text-slate-600">
                                    {/* Earnings */}
                                    <tr className="bg-slate-50/30">
                                        <td className="p-4 pl-8 font-bold text-slate-700" colSpan={3}>Earnings</td>
                                    </tr>
                                    <tr className="border-b border-slate-50 hover:bg-slate-50 transition-colors">
                                        <td className="p-4 pl-8">Basic Salary</td>
                                        <td className="p-4 text-right font-bold text-slate-800">{formatCurrency(result?.monthly.basic || 0)}</td>
                                        <td className="p-4 pr-8 text-right">{formatCurrency(result?.yearly.basic || 0)}</td>
                                    </tr>
                                    <tr className="border-b border-slate-50 hover:bg-slate-50 transition-colors">
                                        <td className="p-4 pl-8">HRA (House Rent Allowance)</td>
                                        <td className="p-4 text-right font-bold text-slate-800">{formatCurrency(result?.monthly.hra || 0)}</td>
                                        <td className="p-4 pr-8 text-right">{formatCurrency(result?.yearly.hra || 0)}</td>
                                    </tr>
                                    <tr className="border-b border-slate-50 hover:bg-slate-50 transition-colors">
                                        <td className="p-4 pl-8">Special Allowance</td>
                                        <td className="p-4 text-right font-bold text-slate-800">{formatCurrency(result?.monthly.specialAllowance || 0)}</td>
                                        <td className="p-4 pr-8 text-right">{formatCurrency(result?.yearly.specialAllowance || 0)}</td>
                                    </tr>
                                    <tr className="bg-green-50/50 border-b border-green-100">
                                        <td className="p-4 pl-8 text-green-700 font-bold">Gross Salary</td>
                                        <td className="p-4 text-right font-bold text-green-700">{formatCurrency(result?.monthly.grossSalary || 0)}</td>
                                        <td className="p-4 pr-8 text-right font-bold text-green-700">{formatCurrency(result?.yearly.grossSalary || 0)}</td>
                                    </tr>

                                    {/* Deductions */}
                                    <tr className="bg-slate-50/30">
                                        <td className="p-4 pl-8 font-bold text-slate-700" colSpan={3}>Deductions</td>
                                    </tr>
                                    <tr className="border-b border-slate-50 hover:bg-slate-50 transition-colors">
                                        <td className="p-4 pl-8">EPF (Employee Provident Fund)</td>
                                        <td className="p-4 text-right text-red-500 font-bold">-{formatCurrency(result?.monthly.employeePf || 0)}</td>
                                        <td className="p-4 pr-8 text-right text-red-500">-{formatCurrency(result?.yearly.employeePf || 0)}</td>
                                    </tr>
                                    <tr className="border-b border-slate-50 hover:bg-slate-50 transition-colors">
                                        <td className="p-4 pl-8">Professional Tax</td>
                                        <td className="p-4 text-right text-red-500 font-bold">-{formatCurrency(result?.monthly.professionalTax || 0)}</td>
                                        <td className="p-4 pr-8 text-right text-red-500">-{formatCurrency(result?.yearly.professionalTax || 0)}</td>
                                    </tr>
                                    <tr className="bg-red-50/50 border-b border-red-100">
                                        <td className="p-4 pl-8 text-red-700 font-bold">Total Deductions</td>
                                        <td className="p-4 text-right font-bold text-red-700">-{formatCurrency(result?.monthly.totalDeductions || 0)}</td>
                                        <td className="p-4 pr-8 text-right font-bold text-red-700">-{formatCurrency(result?.yearly.totalDeductions || 0)}</td>
                                    </tr>

                                    {/* Net Pay */}
                                    <tr className="bg-purple-50/50">
                                        <td className="p-4 pl-8 text-purple-700 font-black text-lg">Net Salary (In-Hand)</td>
                                        <td className="p-4 text-right font-black text-purple-700 text-lg">{formatCurrency(result?.monthly.netSalary || 0)}</td>
                                        <td className="p-4 pr-8 text-right font-black text-purple-700 text-lg">{formatCurrency(result?.yearly.netSalary || 0)}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>

                </div>
            </div>
        </div>

    );
}
